:root {
  --nav-width: 50px;
  --nav-height: 100vh;

  --content-margin-top: 0;
  --content-margin-left: var(--nav-width);

  --min-container-width: 320px;

  --success-color: #08bc0d;
  --warn-color: #f1c410;
  --info-color: #3598db;
  --error-color: #e74d3d;
}

@media (--at-most-tablet) {
  :root {
    --nav-width: 100vw;
    --nav-height: 40px;

    --content-margin-top: var(--nav-height);
    --content-margin-left: 0;
  }
}



